<template>
  <!--  -->
  <a-layout class="container">
    <a-row style="margin: 10px;" justify="center">
      <a-col class="gutter-row" :span="6">
        <span>  </span>
        <a-select
          v-model:value="mainSchema"
          :options="mainSchemas"
          style="width: 200px"
        ></a-select>
      </a-col>
      <a-col class="gutter-row" :span="6">
        <span>  </span>
        <!--
          :max-tag-count="3"  3 
        -->
        <a-select
          v-model:value="mainField"
          :max-tag-count="3"
          :options="mainFields"
          mode="multiple"
          style="width: 200px"
        >
        </a-select>
      </a-col>
      <a-col class="gutter-row" :span="6">
        <span>  </span>
        <a-select
          v-model:value="subSchema"
          :options="subSchemas"
          style="width: 200px"
        ></a-select>
      </a-col>
      <a-col class="gutter-row" :span="6">
        <span>  </span>
        <!--
          :max-tag-count="3"  3 
        -->
        <a-select
          v-model:value="subField"
          :max-tag-count="3"
          :options="subFields"
          mode="multiple"
          style="width: 200px"
        ></a-select>
      </a-col>
    </a-row>
    <!--  -->
    <a-button 
      type="primary" 
      shape="round" 
      :loading="loading"
      class="search-button"
      @click="search(1, pageSize)"
    >
      <template #icon>
        <SearchOutlined />
      </template>
      
    </a-button>
    
    <!--  -->
    <a-pagination 
      v-model:current="current" 
      v-model:pageSize="pageSize"
      :total="infoTotal"
      :showSizeChanger="false"
      :show-total="total => ` ${total} `"
      show-less-items
      show-quick-jumper
      class="pagination"
      @change="(page, pageSize) => pageChange(page, pageSize)"
    />

    <!--  -->
    <a-divider class="divider"></a-divider>

    <!--  -->
    <a-table
      :dataSource="infoGroup" 
      :columns="columns"
      :pagination="false"
      :loading="loading"  
      size="small"
      style="margin-left: 10px; margin-right: 10px;"
      bordered
    >
      <template #bodyCell="{ column, text, record }">
        <template v-if="column.dataIndex === 'mainSchema'">
          <a-tag @click="() => selectRecord(text)">  1 </a-tag>
        </template>
        <template v-else-if="column.dataIndex === 'subSchema'">
          <template v-for="(item, index) in text" :key="index">
            <a-tag @click="() => selectRecord(item)">  {{ index + 1 }} </a-tag>
          </template>
        </template>
      </template>
    </a-table>
    
    <!--  -->
    <!--  -->
    <a-modal
      v-model:visible="recordVisible" 
      title=""
      okText=""
      cancelText=" "
      :destroyOnClose=true
      @ok="() => recordVisible = false"
    >
      <a-list bordered :data-source="selectedRecord">
        <template #renderItem="{ item }">
          <a-list-item>
            <div>{{ item.schema }}-{{ item.field }}</div>  
            <div>
              {{ item.value }} 
              <copy-two-tone @click="() => copyText(item.value)"/>
            </div>
          </a-list-item>
        </template>
      </a-list>
    </a-modal>
  </a-layout>
</template>

<script setup>
import { computed, onMounted, ref } from 'vue';
import axios from 'axios';
import { SearchOutlined, CopyTwoTone } from '@ant-design/icons-vue';
import { message } from 'ant-design-vue';
import { copyText } from '@/utils/copyText';

import qs from 'qs';

// 
const infoTotal = ref(0);
// 
const current = ref(1);
// 
const pageSize = ref(10);

// 
const infoGroup = ref([]);

// 
const typeDatas = ref([]);

// 
const mainSchema = ref("");
// 
const mainField = ref([]);
// 
const subSchema = ref("");
// 
const subField = ref([]);

// 
const selectedRecord = ref([]);

// 
const recordVisible = ref(false);

// 
const loading = ref(false);

// 
const mainSchemas = ref([]);
// 
const mainFields = computed(() => {
  return typeDatas.value.find(val => val.schemaName == mainSchema.value)?.fields.map(val => {
    return {value: val.fieldName};
  });
});
// 
const subSchemas = ref([]);
// 
const subFields = computed(() => {
  return typeDatas.value.find(val => val.schemaName == subSchema.value)?.fields.map(val => {
    return {value: val.fieldName};
  });
});

// 
const columns = [
  {
    title: "",
    dataIndex: "key",
    key: "key",
  },
  {
    title: "",
    dataIndex: "mainSchema",
    key: "mainSchema",
  },
  {
    title: "",
    dataIndex: "subSchema",
    key: "subSchema",
  }
]

// 
function createInfoGroupGroup(data) {
  infoGroup.value = data.map((info, index) => {
    const mainSchemaInfo = [];
    const subSchemaInfo = [];
    for(let val of info) {
      if(!Array.isArray(val)) mainSchemaInfo.push(val);
      else subSchemaInfo.push(val);
    }
    return {
      key: (current.value - 1) * pageSize.value + index + 1,
      mainSchema: mainSchemaInfo,
      subSchema: subSchemaInfo
    }
  });
}

// 
function search(page, pageSize) {
  // 
  loading.value = true;
  axios.post('dataShow.do', {
    page: page,
    pageSize: pageSize,
    value: [
      {schema: mainSchema.value, fields: [...mainField.value]},
      {schema: subSchema.value, fields: [...subField.value]}
    ]
  }).then((result) => {
    infoTotal.value = result.data.infoTotal;
    createInfoGroupGroup(result.data.res);
    loading.value = false;
  }).catch((error) => {
    message.error("");
    loading.value = false;
    console.log(error);
  })
}

//  
function pageChange(page, pageSize) {
  search(page, pageSize);
}

// 
function selectRecord(record) {
  selectedRecord.value = record;
  recordVisible.value = true;
}

// 
onMounted(() => {
  axios.get('getType.do')
    .then((result) => {
      typeDatas.value = result.data.map(val => {
        val.fields = val.fields.map(field => ({
          "fieldName": field
        }));
        return {
          "schemaName": val.schemaName,
          "fields": val.fields
        };
      });
      mainSchemas.value = typeDatas.value.map(val => {
        return {value: val.schemaName};
      });
      subSchemas.value = typeDatas.value.map(val => {
        return {value: val.schemaName};
      });
    })
    .catch((error) => {
      console.log(error);
    })
});
</script>

<style scoped>
.container {
  height: 100%;
}

.search-button {
  margin: 6px auto;
  width: 200px;
}

.pagination {
  margin: 10px auto;
}

.divider {
  margin-top: 0;
  /* border-color: #1890ff; */
}

@media (max-height: 480px) {
  /* .container {
    display: none;
  } */
}
</style>